import store from "../../store/index.js";
import api from "../../api/api.js";
import {
    friendlyDate
} from '../../common/util.js';

export default {
    data() {
        return {
            indicatorDots: true,
            autoplay: true,
            interval: 2000,
            duration: 500,
            scrollLeft: 0,
            currentTab: 0,
            tabClick: 0,
            isHeight: '',
            isLeft: 0,
            agents: [{
                title: '学术论坛',
                list: [],
                id: 0,
                pages: 0
            }, {
                title: '校园动态',
                list: [],
                id: 1,
                pages: 0
            }, {
                title: '二手交易',
                list: [],
                id: 2,
                pages: 0
            }, {
                title: '缺个伴吗',
                list: [],
                id: 3,
                pages: 0
            }, {
                title: '帮你干活',
                list: [],
                id: 4,
                pages: 0
            }, {
                title: '经验交流',
                list: [],
                id: 5,
                pages: 0
            }],
            isWidth: 0,
            imageList: []
        }
    },
    mounted() {
        // 设置swiper高度
        this.isHeight = this.agents[this.currentTab].list.length * 300 + 160 + 'rpx'
        // console.log(this.isHeight)
        var that = this
        // 获取设备宽度
        // uni.getSystemInfo({
        // 	success(e) {
        // 		that.isWidth = e.windowWidth / that.agents.length
        // 	}
        // })
    },
    methods: {
        goAddPost() {
            uni.navigateTo({
                url: 'add-post/add-post'
            });
        },
        goDetail(e) {
            var index = e.currentTarget.dataset.index;
            var postId = this.agents[this.currentTab].list[index].postId;
            console.log(postId);
            var detail = {
                postId
            };
            uni.navigateTo({
                url: 'post-detail/post-detail?query=' + encodeURIComponent(JSON.stringify(detail))
            });
        },
        goSearch() {
            uni.navigateTo({
                url: 'search-post/search-post'
            });
        },
        // 导航栏点击
        navClick(index) {
            this.currentTab = index
            this.tabClick = index
            var that = this
            var url = api.urls.getPostList;
            var data = {
                num: 0,
                postType: that.currentTab
            };
            api.req.get(url, data, (res) => {
                if (res.code === 0) {
                    for (var i = 0; i < res.data.content.length; i++)
                        res.data.content[i].issueTime = friendlyDate(new Date(res.data.content[i].issueTime
                            .replace(/\-/g, '/').replace(/\T/g,
                                ' ').substring(0, 19)).getTime())
                    that.agents[index].list = res.data.content
                    console.log(that.agents[index].list.length)
                }
            }, (fail) => {
                setTimeout(() => {
                    uni.hideLoading()
                    uni.stopPullDownRefresh()
                    uni.showToast({
                        icon: 'none',
                        title: '刷新失败，请稍后再试'
                    });
                }, 4000)
            });
        },
        // swiper 滑动
        swiperTab: function(e) {
            // console.log(e)
            var index = e.detail.current //获取索引
            this.isHeight = this.agents[index].list.length * 300 + 160 + 'rpx' //设置swiper高度
            this.isLeft = index * this.isWidth //设置下划线位置
            this.currentTab = e.detail.current //设置swiper的第几页
            this.tabClick = e.detail.current //设置导航点击了哪一个var url = api.urls.getPostList;

            if (this.currentTab == 4) {
                this.scrollLeft = 150
            } else if (this.currentTab == 5) {
                this.scrollLeft = 150
            }
            if (this.currentTab == 1 || this.currentTab == 0) {
                this.scrollLeft = 0
            }
            var that = this
            var url = api.urls.getPostList;
            var data = {
                num: 0,
                postType: that.currentTab
            };
            api.req.get(url, data, (res) => {
                if (res.code === 0) {
                    for (var i = 0; i < res.data.content.length; i++)
                        res.data.content[i].issueTime = friendlyDate(new Date(res.data.content[i].issueTime
                            .replace(/\-/g, '/').replace(
                                /\T/g,
                                ' ').substring(0, 19)).getTime())
                    that.agents[that.currentTab].list = res.data.content
                    that.isHeight = that.agents[that.currentTab].list.length * 300 + 160 + 'rpx'
                }
            }, (fail) => {
                setTimeout(() => {
                    uni.hideLoading()
                    uni.stopPullDownRefresh()
                    uni.showToast({
                        icon: 'none',
                        title: '刷新失败，请稍后再试'
                    });
                }, 4000)
            });
        }
    },
    onPullDownRefresh() {
        console.log(`刷新第${this.currentTab}项`)
        var that = this
        var urlImg = api.urls.getRollImage
        var dataImg = {
            imagePosition: 'true'
        }
        api.req.get(urlImg, dataImg, (resImg) => {
            console.log(resImg)
            if (resImg.code == 0) {
                that.imageList = resImg.data
            }
        });
        var url = api.urls.getPostList;
        var data = {
            num: 0,
            postType: that.currentTab 
        };
        api.req.get(url, data, (res) => {
            if (res.code === 0) {
                for (var i = 0; i < res.data.content.length; i++)
                    res.data.content[i].issueTime = friendlyDate(new Date(res.data.content[i].issueTime.replace(
                        /\-/g, '/').replace(
                        /\T/g,
                        ' ').substring(0, 19)).getTime())
                that.agents[that.currentTab].list = res.data.content
                that.isHeight = that.agents[that.currentTab].list.length * 300 + 160 + 'rpx'
            }
            uni.stopPullDownRefresh()
        }, (fail) => {
            setTimeout(() => {
                uni.hideLoading()
                uni.stopPullDownRefresh()
                uni.showToast({
                    icon: 'none',
                    title: '刷新失败，请稍后再试'
                });
            }, 4000)
        });
    },
    onReachBottom() {
        console.log(`加载${this.currentTab}`)
        uni.showLoading({
            content: '加载中'
        })
        var that = this
        if (that.agents[that.currentTab].list.length !== 0) {
            var url = api.urls.getPostList;
            var data = {
                num: that.agents[this.currentTab].pages,
                postType: that.currentTab
            };
            api.req.get(url, data, (res) => {
                if (res.code === 0 && res.data.empty == false) {
                    for (var i = 0; i < res.data.content.length; i++)
                        res.data.content[i].issueTime = friendlyDate(new Date(res.data.content[i].issueTime
                            .replace(/\-/g, '/').replace(
                                /\T/g,
                                ' ').substring(0, 19)).getTime())
                    for (var i = 0; i < res.data.content.length; i++)
                        that.agents[that.currentTab].list.push(res.data.content[i])

                    that.agents[this.currentTab].pages = that.agents[this.currentTab].pages + 1
                    that.isHeight = that.agents[that.currentTab].list.length * 300 + 160 + 'rpx'
                    uni.hideLoading()
                } else {
                    uni.hideLoading()
                    uni.showToast({
                        icon: 'none',
                        title: '你已经看到我的底线啦！',
                    });
                }
            }, (fail) => {
                setTimeout(() => {
                    uni.hideLoading()
                    uni.stopPullDownRefresh()
                    uni.showToast({
                        icon: 'none',
                        title: '刷新失败，请稍后再试'
                    });
                }, 4000)
            });
        } else {
            uni.hideLoading()
            uni.showToast({
                icon: 'none',
                title: '你已经看到我的底线啦！',
            });
        }
    },
    onShow() {
        var that = this
        var urlImg = api.urls.getRollImage
        var dataImg = {
            imagePosition: 'true'
        }
        api.req.get(urlImg, dataImg, (resImg) => {
            console.log(resImg)
            if (resImg.code == 0) {
                that.imageList = resImg.data
            }
        });
        var url = api.urls.getPostList;
        var data = {
            num: 0,
            postType: that.currentTab
        };
        api.req.get(url, data, (res) => {
            if (res.code === 0) {
                for (var i = 0; i < res.data.content.length; i++)
                    res.data.content[i].issueTime = friendlyDate(new Date(res.data.content[i].issueTime.replace(
                        /\-/g, '/').replace(
                        /\T/g,
                        ' ').substring(0, 19)).getTime())
                that.agents[this.currentTab].pages = that.agents[this.currentTab].pages + 1
                that.agents[that.currentTab].list = res.data.content
                that.isHeight = that.agents[that.currentTab].list.length * 300 + 160 + 'rpx'
            }
        }, (fail) => {
            setTimeout(() => {
                uni.hideLoading()
                uni.stopPullDownRefresh()
                uni.showToast({
                    icon: 'none',
                    title: '刷新失败，请稍后再试'
                });
            }, 4000)
        });
    }
}
